* {
  font-family: "Pragmatica";
}

.wrapper-item {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
    overflow: auto;
}

.modal-content {
  position: relative;
  min-width: 250px;
  margin: auto;
  padding: 10px 30px;
  background: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  border-radius: 9px;

  max-width: 80%; /* Adjust as needed */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.modal-body {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-close {
  position: absolute;
  width: 10px;
  height: 10px;
  margin: 5px;
  background: transparent;
  border: none;
  top: 0px;
  right: 0px;
  cursor: pointer;
}

