@import "../../../../styles/variables.scss";


.topheader {
  display: flex;
  justify-content: space-between;

  &-form {
    display: flex;
    cursor: pointer;
    padding: 15px 0 15px 0;

    &__item{
      display: flex;
      gap: 5px;
      color: $dark-black;
    }

    &__text:hover {
      color: $dark-orange;
    }
  }
}
