@import "../../../../styles/variables.scss";

.header-icon {
  display: flex;

  &__item {
    padding: 27px;
    border-left: solid 1px $header-border-color;
    max-height: 73px;
    transition: all 0.5s ease-out;
  }
  &__item a {
    color: currentColor;
    
  }
  &__item:hover {
    background-color: $dark-black;
    color: $background-primary-color;
  }
}
